import React from 'react';

import { useState } from 'react';
import { useAppDispatch } from '../../app/hooks';
import { Monster, Pokemon } from '../../models/interfaces/monster.interface';
import { setSelectedMonster } from '../../reducers/monsters/monsters.actions';
import {
  Image,
  ListTitle,
  MonsterCard,
  MonsterName,
  MonstersSection,
} from './MonstersList.styled';

import { setSelectedPokemon } from '../../reducers/monsters/monsters.actions.extended';
import { capitalize } from '@mui/material';

type MonstersListProps = {
  monsters: Monster[];
};

type PokemonListProps = {
  pokemons: Pokemon[];
};

const MonstersList: React.FC<MonstersListProps> = ({ monsters }) => {
  const dispatch = useAppDispatch();

  const [selectedMonsterId, setSelectedMonsterId] = useState<string | null>(
    null,
  );

  const handleMonsterClick = (monster: Monster) => {
    const value = selectedMonsterId === monster.id ? null : monster.id;
    setSelectedMonsterId(value);
    dispatch(setSelectedMonster(!value ? null : monster));
  };

  return (
    <div>
      <ListTitle>
        {monsters.length > 0 ? 'Select your monster' : 'No monsters available'}
      </ListTitle>

      <MonstersSection data-testid="monsters-list-section">
        {monsters.map(monster => (
          <MonsterCard
            key={monster.id}
            onClick={() => handleMonsterClick(monster)}
            selected={monster.id === selectedMonsterId}
            data-testid={monster.id}>
            <Image src={monster.imageUrl} />
            <MonsterName>{monster.name}</MonsterName>
          </MonsterCard>
        ))}
      </MonstersSection>
    </div>
  );
};

const PokemonList: React.FC<PokemonListProps> = ({ pokemons }) => {
  const dispatch = useAppDispatch();

  const [selectedPokemonId, setSelectedPokemonId] = useState<number | null>(
    null,
  );

  const handleMonsterClick = (pokemon: Pokemon) => {
    const value = selectedPokemonId === pokemon.id ? null : pokemon.id;
    setSelectedPokemonId(value);
    dispatch(setSelectedPokemon(!value ? null : pokemon));
  };

  return (
    <div>
      <ListTitle>
        {pokemons.length > 0 ? 'Select your pokemon' : 'No pokemons available'}
      </ListTitle>

      <MonstersSection>
        {pokemons.map(pokemon => (
          <MonsterCard
            key={pokemon.id}
            onClick={() => handleMonsterClick(pokemon)}
            selected={pokemon.id === selectedPokemonId}
            data-testid={pokemon.id}>
            <Image src={pokemon.sprites.front_default} />
            <MonsterName>{capitalize(pokemon.name)}</MonsterName>
          </MonsterCard>
        ))}
      </MonstersSection>
    </div>
  );
};

export { MonstersList, PokemonList };
