import { API_URL, POKEMON_API_URL } from '../../constants/env';
import { Battle, Players } from '../../models/interfaces/battle.interface';
import { Pokemon } from '../../models/interfaces/monster.interface';

const battle = (param: Players): Promise<Battle> =>
  fetch(`${API_URL}/battle`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(param),
  }).then(response => response.json());

const getSome = async (times: number[]): Promise<Pokemon[]> => {
  //await fetch(`${API_URL}/monsters`).then(response => response.json());
  //return monstersData.monsters;
  const result = [];
  let temp;
  for (let i = 0; i < times.length; i++) {
    console.log('calling pokemon:' + times[i]);
    temp = await fetch(`${POKEMON_API_URL}pokemon/${times[i]}/`).then(
      response => response.json(),
    );
    result.push(temp);
  }
  return result;
};

export const MonsterServiceExtended = {
  battle,
  getSome,
};
