import React, { useState } from 'react';

import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useAppDispatch } from '../../app/hooks';
import { MonsterBattleCard } from '../../components/monster-battle-card/MonsterBattleCard.extended';
import {
  MonstersList,
  PokemonList,
} from '../../components/monsters-list/MonstersList.extended';
import { Title } from '../../components/title/Title';
import { fetchMonstersData } from '../../reducers/monsters/monsters.actions';
import { Monster, Pokemon } from '../../models/interfaces/monster.interface';
import {
  selectMonsters,
  selectSelectedMonster,
} from '../../reducers/monsters/monsters.selectors';
import {
  selectRandomMonster,
  monsterWins,
  selectPokemons,
  selectSelectedPokemon,
  selectRandomPokemon,
  pokemonWins,
} from '../../reducers/monsters/monsters.selectors.extended';
import {
  setRandomMonster,
  fetchBattleWins,
  setWinner,
  fetchPokemonData,
  setRandomPokemon,
  setWinnerPokemon,
} from '../../reducers/monsters/monsters.actions.extended';
import {
  BattleSection,
  PageContainer,
  StartBattleButton,
  Hit,
  Splotch1,
  Splotch2,
} from './BattleOfMonsters.extended.styled';
import { Battle, Battle2 } from '../../models/interfaces/battle.interface';
import { WinnerDisplay } from '../../components/winner-display/WinnerDisplay.extended';
import { PokemonBattleCard } from '../../components/pokemon-battle-card/PokemonBattleCard.extended';

const BattleOfMonsters = () => {
  const dispatch = useAppDispatch();

  const monsters = useSelector(selectMonsters);
  const selectedMonster = useSelector(selectSelectedMonster);
  const selectedEnemyMonster = useSelector(selectRandomMonster);
  const wins = useSelector(monsterWins);

  const pokemons = useSelector(selectPokemons);
  const selectedPokemon = useSelector(selectSelectedPokemon);
  const selectedEnemyPokemon = useSelector(selectRandomPokemon);
  const winsPokemon = useSelector(pokemonWins);

  //state to control the animation
  const [isAnimating, setIsAnimating] = useState(false);

  useEffect(() => {
    const times = [];
    for (let i = 0; i < 5; i++) {
      const random = Math.floor(Math.random() * 1027);
      times.push(random);
    }
    dispatch(fetchMonstersData());
    dispatch(fetchPokemonData(times));
  }, []);

  useEffect(() => {
    let enemies = [];
    let enemy;
    if (selectedPokemon) {
      enemies = pokemons.filter(
        (element: Pokemon) => element.id !== selectedPokemon.id,
      );
      //calculating the random number
      const random = Math.floor(Math.random() * (enemies.length - 1));
      enemy = enemies[random];
      dispatch(setRandomPokemon(enemy));
    }
    dispatch(setWinnerPokemon(null));
  }, [selectedPokemon]);

  const handleStartBattleClick = async () => {
    //calling the two monsters
    console.log('selected');
    console.log(selectedPokemon);
    console.log('computer');
    console.log(selectedEnemyMonster);
    let result: Battle2;
    if (selectedPokemon && selectedEnemyPokemon) {
      //copying the original battle logic here (it would be better to have it on a separated doc)
      const pokemon1Sum =
        selectedPokemon.stats[1].base_stat +
        selectedPokemon.stats[2].base_stat +
        selectedPokemon.stats[0].base_stat +
        selectedPokemon.stats[5].base_stat;
      const pokemon2Sum =
        selectedEnemyPokemon.stats[1].base_stat +
        selectedEnemyPokemon.stats[2].base_stat +
        selectedEnemyPokemon.stats[0].base_stat +
        selectedEnemyPokemon.stats[5].base_stat;

      if (pokemon1Sum > pokemon2Sum) {
        result = { winner: selectedPokemon, tie: false };
      } else if (pokemon2Sum > pokemon1Sum) {
        result = { winner: selectedEnemyPokemon, tie: false };
      } else {
        result = { winner: null as any, tie: true };
      }
      //original logic
      /* const result = await dispatch(
        fetchBattleWins({
          monster1Id: selectedMonster.id,
          monster2Id: selectedEnemyMonster.id,
        }),
      ); */
      setTimeout(() => dispatch(setWinnerPokemon(result)), 2000);
      setIsAnimating(true);
      setTimeout(() => setIsAnimating(false), 2000);
    }
  };

  const displayWinner = () => {
    if (winsPokemon) {
      if (!winsPokemon.tie) {
        return <WinnerDisplay text={winsPokemon.winner.name} />;
      } else {
        return <WinnerDisplay text="No one" />;
      }
    }
  };

  return (
    <PageContainer>
      <Hit isAnimating={isAnimating}>
        <Splotch1 isAnimating={isAnimating} />
        <Splotch2 isAnimating={isAnimating} />
      </Hit>
      <Title>Battle of Monsters</Title>
      {/* <MonstersList monsters={monsters} /> */}
      <PokemonList pokemons={pokemons} />
      {displayWinner()}
      <BattleSection>
        <PokemonBattleCard
          pokemon={selectedPokemon || null}
          title={selectedPokemon?.name || 'Player'}></PokemonBattleCard>
        <StartBattleButton
          data-testid="start-battle-button"
          disabled={selectedPokemon === null}
          onClick={handleStartBattleClick}>
          Start Battle
        </StartBattleButton>
        <PokemonBattleCard
          pokemon={selectedEnemyPokemon || null}
          title={selectedEnemyPokemon?.name || 'Computer'}></PokemonBattleCard>
      </BattleSection>
    </PageContainer>
  );
};

export { BattleOfMonsters };
