import React, { useEffect } from 'react';

import { Pokemon } from '../../models/interfaces/monster.interface';
import {
  BattleMonsterCard,
  BattleMonsterTitle,
  ProgressBar,
  Image,
  CardBody,
} from './PokemonBattleCard.extended.styled';
import { capitalize } from '@mui/material';

type PokemonCardProps = {
  pokemon?: Pokemon | null;
  title?: string;
};

function validate(a: number | undefined) {
  return a ? a : 0;
}

const printStatuses = (pokemon: Pokemon) => {
  return (
    <CardBody>
      <p>HP:</p>
      <ProgressBar
        variant="determinate"
        value={validate(pokemon.stats[0].base_stat)}></ProgressBar>
      <p>Attack</p>
      <ProgressBar
        variant="determinate"
        value={validate(pokemon.stats[1].base_stat)}></ProgressBar>
      <p>Defense</p>
      <ProgressBar
        variant="determinate"
        value={validate(pokemon.stats[2].base_stat)}></ProgressBar>
      <p>Speed</p>
      <ProgressBar
        variant="determinate"
        value={validate(pokemon.stats[5].base_stat)}></ProgressBar>
    </CardBody>
  );
};

const PokemonBattleCard: React.FC<PokemonCardProps> = ({ pokemon, title }) => {
  if (pokemon) {
    return (
      <BattleMonsterCard>
        <Image src={pokemon.sprites.front_default}></Image>
        <BattleMonsterTitle>{capitalize(title!)}</BattleMonsterTitle>
        <hr style={{ color: '000000', opacity: '20%' }} />
        {printStatuses(pokemon)}
      </BattleMonsterCard>
    );
  } else {
    return (
      <BattleMonsterCard centralized>
        <BattleMonsterTitle>{title!}</BattleMonsterTitle>
      </BattleMonsterCard>
    );
  }
};

export { PokemonBattleCard };
