import styled from '@emotion/styled';
import { Button, Hidden, Typography } from '@mui/material';
import { colors } from '../../constants/colors';
import { keyframes } from '@mui/system';

export const PageContainer = styled.div(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '35px',
  maxWidth: '820px',
  margin: '0 auto',
}));

export const BattleSection = styled.section(() => ({
  display: 'flex',
  alignItems: 'center',
  gap: '25px',
}));

export const StartBattleButton = styled(Button)(({ disabled }) => ({
  background: disabled ? colors.lightGreen : colors.darkGreen,
  boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  borderRadius: '5px',
  fontFamily: 'Roboto',
  fontStyle: 'normal',
  fontWeight: '400',
  fontSize: '18px',
  lineHeight: '21px',
  color: `${colors.white} !important`,
  padding: '12px 30px',
  textTransform: 'capitalize',
  '&:hover': {
    background: colors.darkGreenHover,
  },
}));

export const Hit = styled('div')(
  ({ isAnimating }: { isAnimating: boolean }) => ({
    animation: isAnimating ? `${fadeIn} 3s ease-in-out` : 'none',
    display: isAnimating ? 'block' : 'none',
    position: 'absolute',
    top: '20vh',
    left: '0',
    marginLeft: 'calc(((100vw - 60vh) / 2))',
    width: '60vh',
    height: '60vh',
    boxShadow: '40px 20px 40px 25px #3c0707',
    borderRadius: '50%',
    backgroundColor: 'red',
    zIndex: isAnimating ? '1' : '-1',
    '&::before': {
      content: '" "',
      position: 'absolute',
      backgroundColor: 'red',
      borderRadius: '50%',
      width: '40px',
      height: '60px',
      bottom: '-50px',
      left: '20px',
      boxShadow: '10px 10px 30px 5px #3c0707',
    },
    '&::after': {
      content: '" "',
      position: 'absolute',
      backgroundColor: 'red',
      borderRadius: '50%',
      width: '30px',
      height: '40px',
      bottom: '-30px',
      right: '10px',
      boxShadow: '30px 20px 30px 50px #3c0707',
    },
  }),
);

export const Splotch1 = styled('div')(
  ({ isAnimating }: { isAnimating: boolean }) => ({
    animation: isAnimating ? `${fadeIn} 3s ease-in-out` : 'none',
    position: 'absolute',
    width: '60px',
    height: '50px',
    backgroundColor: 'red',
    borderRadius: '50%',
    top: '-20px',
    left: '-40px',
    boxShadow: '5px 10px 20px 15px #3c0707',
  }),
);

export const Splotch2 = styled('div')(
  ({ isAnimating }: { isAnimating: boolean }) => ({
    animation: isAnimating ? `${fadeIn} 3s ease-in-out` : 'none',
    position: 'absolute',
    width: '40px',
    height: '40px',
    backgroundColor: 'red',
    borderRadius: '50%',
    top: '-30px',
    right: '-20px',
    boxShadow: '15px 10px 40px 20px #3c0707',
  }),
);

const fadeIn = keyframes`
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  30% {
    opacity: 0.9;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
`;
