import { createAction, createAsyncThunk } from '@reduxjs/toolkit';
import { Monster, Pokemon } from '../../models/interfaces/monster.interface';
import { MonsterServiceExtended } from './monsters.service.extended';
import {
  Battle,
  Battle2,
  Players,
} from '../../models/interfaces/battle.interface';

export const fetchBattleWins = createAsyncThunk<Battle, Players>(
  'monsters/fetchBattleWins',
  MonsterServiceExtended.battle,
);

export const setRandomMonster = createAction<Monster | null>(
  'monsters/setRandomMonster',
);

export const fetchPokemonData = createAsyncThunk<Pokemon[], number[]>(
  'monsters/fetchPokemonData',
  MonsterServiceExtended.getSome,
);

export const setWinner = createAction<Battle | null>('monsters/setWinner');

export const setSelectedPokemon = createAction<Pokemon | null>(
  'monsters/setSelectedPokemon',
);

export const setRandomPokemon = createAction<Pokemon | null>(
  'monsters/setRandomPokemon',
);

export const setWinnerPokemon = createAction<Battle2 | null>(
  'monsters/setWinnerPokemon',
);
