import { createReducer } from '@reduxjs/toolkit';
import { Battle, Battle2 } from '../../models/interfaces/battle.interface';
import { Monster, Pokemon } from '../../models/interfaces/monster.interface';
import {
  setRandomMonster,
  fetchBattleWins,
  setWinner,
  fetchPokemonData,
  setSelectedPokemon,
  setRandomPokemon,
  setWinnerPokemon,
} from './monsters.actions.extended';

interface MonsterState {
  selectRandomMonster: Monster | null;
  winner: Battle | null;
  pokemons: Pokemon[];
  selectPokemon: Pokemon | null;
  selectRandomPokemon: Pokemon | null;
  winnerPokemon: Battle2 | null;
}

const initialState: MonsterState = {
  selectRandomMonster: null,
  winner: null,
  pokemons: [],
  selectPokemon: null,
  selectRandomPokemon: null,
  winnerPokemon: null,
};

export const monstersReducerExtended = createReducer(initialState, builder => {
  builder.addCase(setRandomMonster, (state, action) => ({
    ...state,
    selectRandomMonster: action.payload,
  }));

  builder.addCase(fetchBattleWins.fulfilled, (state, action) => ({
    ...state,
    winner: action.payload,
  }));

  builder.addCase(setWinner, (state, action) => ({
    ...state,
    winner: action.payload,
  }));

  builder.addCase(fetchPokemonData.fulfilled, (state, action) => ({
    ...state,
    pokemons: action.payload,
  }));

  builder.addCase(setSelectedPokemon, (state, action) => ({
    ...state,
    selectPokemon: action.payload,
  }));

  builder.addCase(setRandomPokemon, (state, action) => ({
    ...state,
    selectRandomPokemon: action.payload,
  }));

  builder.addCase(setWinnerPokemon, (state, action) => ({
    ...state,
    winnerPokemon: action.payload,
  }));
});
