import { RootState } from '../../app/store';

export const monsterWins = (state: RootState) => state.monstersExtended.winner;

export const selectRandomMonster = (state: RootState) =>
  state.monstersExtended.selectRandomMonster;

export const selectPokemons = (state: RootState) =>
  state.monstersExtended.pokemons;

export const selectSelectedPokemon = (state: RootState) =>
  state.monstersExtended.selectPokemon;

export const selectRandomPokemon = (state: RootState) =>
  state.monstersExtended.selectRandomPokemon;

export const pokemonWins = (state: RootState) =>
  state.monstersExtended.winnerPokemon;
